                        float: left;
                        color: #ffffff;
                        line-height: 30px;
                        border-bottom: 1px solid transparent;
                        &.active{
                            border-bottom: 1px solid #fff;
                        }
                    }
                }
            }

        }
        .innerBox{
            z-index: 3;
            position: absolute;
            top: 150px;
            margin-left: -600px;
            width: 1200px;
            height: 200px;
            // background: #000;
            left: 50%;
            .title1{
                padding-left: 270px;
                width: 100%;
                color: #fff;
                height: 40px;
                line-height: 40px;
                font-size: 24px;
                box-sizing: border-box;
            }
            .title2{
                padding-left: 270px;
                width: 100%;
                color: #fff;
                height: 75px;
                line-height: 75px;
                font-size: 55px;
                box-sizing: border-box;
            }
            .title3{
                padding-left: 720px;
                width: 100%;
                color: #fff;
                height: 40px;
                line-height: 40px;
                font-size: 24px;
                box-sizing: border-box;
                .more{
                    width: 26px;
                    vertical-align: text-bottom;
                   display: inline-block;
                }
            }

        }
        .slideBox {
			width: 100%;
			height: auto;
			overflow: hidden;
			position: relative;
			border: 1px solid #ddd;
		}

		.slideBox .hd {
			height: 200px;
            overflow: hidden;
            position: absolute;
            top: 50%;
            left: 50%;
            margin-left: 550px;
            z-index: 3;
		}

		.slideBox .hd ul {
			overflow: hidden;
			zoom: 1;
			float: left;
		}

		.slideBox .hd ul li {
			margin-top: 4px;
			margin-right: 2px;
			width: 10px;
			height: 10px;
			border-radius: 50%;
			line-height: 8px;
			text-align: center;
			border: 1px solid #fff;
			cursor: pointer;
		}

		.slideBox .hd ul li.on {
			background: #fff;
			color: #fff;
		}

		.slideBox .bd {
			position: relative;
			height: 100%;
			z-index: 0;
		}

		.slideBox .bd li {
			zoom: 1;
			vertical-align: middle;
		}

		.slideBox .bd img {
			width: 100%;
			display: block;
        }
        
    }
}
