        /* 下面是前/后按钮代码，如果不需要删除即可 */
		.slideBox .prev{
            position:absolute; left:3%; top:50%; margin-top:-50px; display:block; width:26px; height:46px; background:url(../img/arrowl.png)  no-repeat; filter:alpha(opacity=50);opacity:0.5;   
        }
		.slideBox .next{ position:absolute; left:3%; top:50%; margin-top:-50px; display:block; width:26px; height:46px; background:url(../img/arrowr.png)  no-repeat; filter:alpha(opacity=50);opacity:0.5;   }
		.slideBox .next{ left:auto; right:3%;  }
		.slideBox .prev:hover,
		.slideBox .next:hover{ filter:alpha(opacity=100);opacity:1;  }
		.slideBox .prevStop{ display:none;  }
		.slideBox .nextStop{ display:none;  }
    }
