                        border: 1px solid #0073A9!important;
                    }
                    
                    #prePage,
                    #nextPage {
                        width: 70px;
                        color: #0073A9;
                        border: 1px solid #0073A9!important;
                    }
                    
                    .page_div .current {
                        background-color: #0073A9;
                        border-color: #0073A9;
                        color: #FFFFFF;
                    }
                    
                    .totalPages {
                        font-size: 15px;
                        margin: 0 10px;
                    }
                    
                    .totalPages span,
                    .totalSize span {
                        color: #0073A9;
                        margin: 0 5px;
                    }
                }
            }
            .contentDes{
                border-bottom: 1px solid #3a4b83;
                padding: 50px 0 0 10px;
                .title1{
                    font-size: 30px;
                    font-weight: 500;
                    color: #3a3a3b;
                    height: 55px;
                    line-height: 55px;
                    text-align: center;
                }
                .time{
                    color: #707070;
                    font-size: 18px;
                    height: 40px;
                    line-height: 40px;
                    text-align: center;
                }
                .newsDes{
                    padding-bottom: 50px;
                    width: 100%;
                    p{
                        margin-top: 30px;
                        color: #3a3a3b;
                        line-height: 40px;
                        text-indent: 2em;
                    }
                }
                
            }
            .otherNews{
                margin-top: 20px;
                width: 100%;
                .preNews,.nextNews{
                    height: 40px;
                    line-height: 40px;
                    .pre,.next{
                        margin-right: 10px;
                        display: inline-block;
                        color: #3a3a3b;
                        font-size: 18px;
                    }
                    .name{
                        &.color1{
                            color: #3a3a3b
                        }
                        color: #3a4b83
                    }
                }
            }
        }
    }
}



